import { useCallback } from 'react';
import * as Sentry from '@sentry/react';
import {
  NoumLayoutStatusFilter,
  type MoveToolToNoumLayoutColumnInput,
  NoumLayoutStatus,
  type ElementOutput,
} from '@/apollo/generated/types';
import {
  GetSpaceByIdDocument,
  type GetSpaceByIdQuery,
  type GetSpaceByIdQueryVariables,
  useMoveToolToNoumLayoutColumnMutation,
} from '@/apollo/graphql';
import { useToast } from '@/hooks/toast';
import { cloneDeep } from 'lodash';
import {
  getColumn,
  getSection,
} from '@/screens/Chamber/components/SectionElementRearrange/rearrangeHelper';
import { type DraggableLocation } from 'react-beautiful-dnd';
import { ElementUtils } from '@/utils/element';

export function useMoveToolToNoumLayoutColumnHelper() {
  const { addToast } = useToast();

  const [moveToolToNoumLayoutColumn, { loading }] =
    useMoveToolToNoumLayoutColumnMutation();

  const moveToolToNoumLayoutColumnHelper = useCallback(
    async (
      input: MoveToolToNoumLayoutColumnInput,
      spaceId: string,
      source: DraggableLocation,
      dest: DraggableLocation,
    ) => {
      let isSuccess;
      try {
        isSuccess = await moveToolToNoumLayoutColumn({
          variables: { input },
          update: (cache, { data }) => {
            if (!data || !data.moveToolToNoumLayoutColumn) return;

            const variables: GetSpaceByIdQueryVariables = {
              id: spaceId,
              editorV2Enabled: true,
              status: NoumLayoutStatusFilter.Unpublished,
            };

            const cacheResult = cache.readQuery<GetSpaceByIdQuery>({
              query: GetSpaceByIdDocument,
              variables,
            });

            if (!cacheResult) return;

            const { getSpaceById: spaceData } = cacheResult;

            if (!spaceData) return;

            const cloneSpaceData = cloneDeep(spaceData);
            if (!cloneSpaceData?.layout) return;
            cloneSpaceData.layout.status = NoumLayoutStatus.Unsaved;
            const { section: sourceSection } = getSection(
              source.droppableId,
              cloneSpaceData?.layout.sections,
            );
            const { column: sourceColumn } = getColumn(
              source.droppableId,
              sourceSection?.columns,
            );
            if (!sourceColumn) return;
            const elementIndex = sourceColumn.tools.findIndex(
              (element) => element._id === input.toolId,
            );
            const [tool] = sourceColumn.tools.splice(elementIndex, 1);
            const { section: destSection, sectionIndex: destSectionIndex } =
              getSection(dest.droppableId, cloneSpaceData?.layout.sections);
            const { column: destColumn, columnIndex: destColumnIndex } =
              getColumn(dest.droppableId, destSection?.columns);
            if (!destColumn?.tools) return;

            destColumn?.tools.sort(ElementUtils.sortPublished);
            const sortedPosition = destColumn.tools[dest.index]?.position || 0;
            destColumn?.tools.splice(dest.index, 0, {
              ...tool,
              position: sortedPosition,
            });

            if (!destColumn || !destSection) return;
            destColumn.tools.reduce((acc: ElementOutput[], obj) => {
              if (obj.position && sortedPosition) {
                if (obj.position >= sortedPosition && tool._id !== obj._id) {
                  const cloneObj = obj;
                  if (cloneObj.position) cloneObj.position += 1;
                }
              }
              acc.push(obj);
              return acc;
            }, []);
            destSection.columns[destColumnIndex] = destColumn;
            cloneSpaceData.layout.sections[destSectionIndex] = destSection;
            cache.writeQuery({
              query: GetSpaceByIdDocument,
              variables,
              data: {
                getSpaceById: cloneSpaceData,
              },
            });
          },
        });
      } catch (error) {
        let message = 'Unknown';
        if (error instanceof Error) {
          message = error.message;
        }
        addToast('error', 'none', message);
        Sentry.captureException(new Error(message), {
          tags: {
            section: 'moveToolToNoumLayoutColumnMutation',
          },
        });

        isSuccess = false;
      }

      return isSuccess;
    },
    [addToast, moveToolToNoumLayoutColumn],
  );

  return {
    loading,
    moveToolToNoumLayoutColumnHelper,
  };
}

export default useMoveToolToNoumLayoutColumnHelper;
