import { useCallback } from 'react';
import * as Sentry from '@sentry/react';
import { cloneDeep } from 'lodash';
import {
  GetSpaceByIdDocument,
  type GetSpaceByIdQuery,
  useUpdateNoumLayoutSectionMutation,
  type GetSpaceByIdQueryVariables,
} from '@/apollo/graphql';
import {
  NoumLayoutStatusFilter,
  type UpdateNoumLayoutSectionInput,
} from '@/apollo/generated/types';
import { useToast } from '@/hooks/toast';

export function useUpdateNoumSectionHelper() {
  const { addToast } = useToast();

  const [updateSectionLayout, { loading }] =
    useUpdateNoumLayoutSectionMutation();

  const updateNoumSectionHelper = useCallback(
    async (spaceId: string, input: UpdateNoumLayoutSectionInput) => {
      let isSuccess;

      try {
        await updateSectionLayout({
          variables: { input },
          update: (cache, { data }) => {
            if (!data || !data.updateNoumLayoutSection) return;

            const variables: GetSpaceByIdQueryVariables = {
              id: spaceId,
              editorV2Enabled: true,
              status: NoumLayoutStatusFilter.Unpublished,
            };

            const cacheResult = cache.readQuery<GetSpaceByIdQuery>({
              query: GetSpaceByIdDocument,
              variables,
            });

            if (!cacheResult) return;

            const { getSpaceById: spaceData } = cacheResult;

            if (!spaceData) return;

            const cloneSpaceData = cloneDeep(spaceData); // spaceData is the read only object

            cache.writeQuery({
              query: GetSpaceByIdDocument,
              variables,
              data: {
                getSpaceById: cloneSpaceData,
              },
            });
          },
        });
        isSuccess = true;
      } catch (error) {
        let message = 'Unknown';
        if (error instanceof Error) {
          message = error.message;
        }
        addToast('error', 'none', message);
        Sentry.captureException(new Error(message), {
          tags: {
            section: 'updateNoumLayoutSectionMutation',
          },
        });

        isSuccess = false;
      }

      return isSuccess;
    },
    [addToast, updateSectionLayout],
  );
  return {
    loading,
    updateNoumSectionHelper,
  };
}

export default useUpdateNoumSectionHelper;
