import { useCallback } from 'react';
import { t } from 'i18next';
import * as Sentry from '@sentry/react';
import { useLaunchDarkly } from '@/hooks/launchDarkly';
import { useToast } from '@/hooks/toast';
import { useGetNoumTransactionFeeDetailsLazyQuery } from '@/apollo/graphql';
import { Status_Noum } from '@/apollo/generated/types';

export function useCheckChamberCanBePublished() {
  const { addToast } = useToast();
  const { flags } = useLaunchDarkly();

  const handleError = useCallback(
    (networkError: String | Error | null) => {
      addToast(
        'error',
        'none',
        `${t('noumena.toast_error.text')}: ${networkError}`,
      );
    },
    [addToast],
  );

  const handleSuspendedNoum = useCallback(
    (status: string) => {
      addToast('error', 'none', `This Noum is ${status} by Admin`);
    },
    [addToast],
  );

  const handleDeliquentNoum = useCallback(() => {
    addToast(
      'error',
      'none',
      `${t('noumena.money.myplans.handleExpiredNoum')}`,
    );
  }, [addToast]);

  const [gqlGetNoumTransactionFeeDetails] =
    useGetNoumTransactionFeeDetailsLazyQuery();

  const checkChamberCanBePublished = useCallback(
    async (
      chamberId: string | undefined | null,
      followRegularPublishFLow: Function,
      setSetUpSlotForNoum: Function,
      handleClosePublish: Function,
    ) => {
      if (flags.paymentSubscriptions && chamberId) {
        await gqlGetNoumTransactionFeeDetails({
          fetchPolicy: 'network-only',
          variables: { noumDetailInput: { chamber_id: chamberId } },
          onError: ({ networkError = null, graphQLErrors = [] }) => {
            const [err] = graphQLErrors;
            handleError(err?.message ?? networkError);
            Sentry.captureException(new Error(err?.message ?? networkError), {
              tags: {
                section: 'gqlGetNoumTransactionFeeDetails',
              },
            });
          },
          onCompleted: (response) => {
            switch (response?.getNoumTransactionFeeDetails[0]?.status) {
              case Status_Noum.Suspended:
                handleSuspendedNoum(
                  response?.getNoumTransactionFeeDetails[0]?.status,
                );
                break;
              case Status_Noum.Inactive:
                handleSuspendedNoum(
                  response?.getNoumTransactionFeeDetails[0]?.status,
                );
                break;
              case Status_Noum.Active:
                if (response?.getNoumTransactionFeeDetails[0]?.is_publishable) {
                  followRegularPublishFLow();
                } else {
                  setSetUpSlotForNoum(true);
                }
                break;
              case Status_Noum.Delinquent:
                handleDeliquentNoum();
                break;
              case Status_Noum.Archived:
                handleDeliquentNoum();
                break;
              default:
                handleClosePublish();
                setSetUpSlotForNoum(true);
                break;
            }
          },
        });
      } else {
        followRegularPublishFLow();
      }
    },
    [
      flags.paymentSubscriptions,
      gqlGetNoumTransactionFeeDetails,
      handleDeliquentNoum,
      handleError,
      handleSuspendedNoum,
    ],
  );

  return { checkChamberCanBePublished };
}

export default useCheckChamberCanBePublished;
