import { Header } from '@/components/Header';
import { Spinner } from '@/components/Spinner';
import { useNoumEdit } from '@/features/noums/hooks/noums';
import { Stack } from '@/layout';
import ChamberViewLayout from '@/layout/ChamberViewLayout';
import { type LinkedNoumOptionType } from '@/screens/LinkNoum/components/types';
import { ElementUtils } from '@/utils/element';
import { useCallback, useEffect, useMemo, useState, type FC } from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate, generatePath } from 'react-router';
import routes from '@/constants/routes';
import {
  useRemoveSectionHelper,
  useRemoveToolFromNoumLayoutHelper,
} from '@/features/noums/hooks/spaceQuery';
import { SpaceTypeEnum } from '@/apollo/generated/types';
import { NoumAdsModal } from '../components/modals/NoumAds';
import { ChamberVisibilityInviteModal } from '../components/modals/ChamberVisibilityInvite';
import { ChamberPermissionModal } from '../components/modals/ChamberPermission';
import ChamberEditMode from '../components/modals/ChamberEditMode';
import { ChamberArchiveModal } from '../components/modals/ChamberArchive';
import { EditHeaderNew } from './EditHeaderNew';
import { DeleteModal } from '../components/DeleteModal';
import { CreateBroadcastModal } from '../components/modals/ChamberBroadcast/CreateBroadcast';
import { ViewBroadcastModal } from '../components/modals/ChamberBroadcast/ViewBroadcast';
import UnlinkOnArchiveNoumModal from '../components/modals/LinkNoum/UnlinkOnArchiveNoumModal';
import { ManageMembersModal } from '../components/modals/ManageMembersModal';
import { NoumenaCopilot } from '../components/NoumenaCopilot';
import { SectionToolType } from '../components/NoumSections/constants';
import { NoumSidePanel } from '../components/NoumSidePanel';
import { type SectionToolProps } from '../components/SectionElementRearrange/types';
import { ThemePanel } from '../components/ThemePanel';
import errorMessage from './constants';
import { EditChamberHeader } from './EditChamberHeader';
import { EditNoumBody } from './EditNoumBody';
import { EditChamberProvider } from './provider';
import { AppStyled } from './styles';
import { type IEditChamber } from './types';

const EditNoum: FC<IEditChamber> = ({ id }) => {
  const {
    space,
    hasDraft,
    hasPublished,
    hasUnsaved,
    hasUnsavedPermissions,
    loading,
    publishedDate,
    lastChangedDate,
    isOpen,
    isOwner,
    isArchived,
    setOpenArchive,
    isOnLoad,
    openArchive,
    handleClose,
    sideBarOptionSelected,
    onArchive,
    openInvites,
    setOpenInvites,
    openPermission,
    setOpenPermission,
    openManageMembers,
    setOpenManageMembers,
    openNoumAds,
    setOpenNoumAds,
    openBroadcast,
    setOpenBroadcast,
    isVisited,
    handleMarkAsVisited,
    campaigns,
    campaignsLoading,
    fetchMoreCampaigns,
    infiniteState,
    onRefetchCampaigns,
    setIsRestored,
    isRestored,
    linkedNoumsCount,
    unlinkOnArchive,
    unlinkArchivingLoader,
    links,
    openThemePanel,
    setOpenThemePanel,
    isNavigateToCustomPreview,
    setNavigateToCustomPreview,
    isSettingTheme,
    markSpaceAsEditedLoading,
    connections,
    approvedConnectionsLoading,
    approvedConnections,
    approvedInfiniteState,
    fetchMoreApprovedConnections,
    refetchSpaceById,
    openNoumenaCopilot,
    setOpenNoumenaCopilot,
    openRestoreModal,
    setOpenRestoreModal,
    hasDraftNoumLayout,
    hasUnsavedNoumLayout,
    hasPublishedNoumLayout,
    setsectionStatus,
  } = useNoumEdit(id);

  const { t } = useTranslation();
  const [showNewBroadcast, setShowNewBroadcast] = useState(false);
  const [emptyElementErrorMessage, setEmptyElement] = useState<string>('');
  const [noumSidePanelId, setNoumSidePanelId] = useState<string | undefined>();
  const [noumSectionToolType, setNoumSectionToolType] =
    useState<SectionToolProps>();
  const [isDeleteModal, setisDeleteModal] = useState(false);
  const [deletedId, setDeletedId] = useState('');
  const navigate = useNavigate();
  const { removeSectionHelper, loading: removeSectionLoading } =
    useRemoveSectionHelper();
  const { removeToolFromNoumLayoutHelper, loading: removeToolLoading } =
    useRemoveToolFromNoumLayoutHelper();
  useEffect(() => {
    setEmptyElement('');
    const invalidElements = ElementUtils.isInvalidTool(
      space?.layout?.sections!,
    );
    if (invalidElements?.length) {
      setEmptyElement(errorMessage(invalidElements?.[0]?.elementType));
    }
  }, [space, t]);

  const closeNewBroadcast = () => {
    setOpenBroadcast(true);
    setShowNewBroadcast(false);
  };

  const handleChamberRestore = () => {
    setIsRestored(true);
    refetchSpaceById();
  };
  const linkedNoums = useMemo(
    () =>
      links?.linkedNoums.map(
        (item) =>
          ({
            ...item,
          } as LinkedNoumOptionType),
      ) || [],
    [links],
  );

  const handleNavigate = useCallback(
    (noumId: string, route?: string) => {
      if (noumId && isNavigateToCustomPreview)
        generatePath(routes.NOUM_CUSTOM_PREVIEW, { id });
      else if (route && space?.type === SpaceTypeEnum.Project) {
        navigate(route, { replace: true });
      } else {
        navigate(-1);
      }
    },
    [id, isNavigateToCustomPreview, navigate, space?.type],
  );

  const deleteHandler = async () => {
    if (!deletedId || deletedId === '') return;
    let isSuccess = false;
    if (space?._id) {
      if (noumSectionToolType === SectionToolType.SECTION_TYPE) {
        isSuccess = await removeSectionHelper(deletedId, space._id);
      } else if (noumSectionToolType === SectionToolType.TOOL_TYPE) {
        isSuccess = await removeToolFromNoumLayoutHelper(deletedId, space._id);
      } else return;
    }
    if (isSuccess) {
      setisDeleteModal(false);
    }
  };

  if (loading && !space) {
    return <Spinner />;
  }

  return (
    <EditChamberProvider
      space={space}
      loading={loading}
      id={id}
      setisDeleteModal={setisDeleteModal}
      setNoumSectionToolType={setNoumSectionToolType}
      setNoumSidePanelId={setNoumSidePanelId}
      noumSidePanelId={noumSidePanelId}
      refetchSpaceById={refetchSpaceById}
      setDeletedId={setDeletedId}
      setsectionStatus={setsectionStatus}
    >
      {space && isOwner && !isArchived && (
        <AppStyled
          data-testid="EDIT-CHAMBER"
          className="App"
          applyMinHeight={false}
          aria-label="noum-layout"
        >
          <Header
            isBorderRadius={false}
            style={{ padding: `0px` }}
            aria-label="noum-header"
          >
            <EditHeaderNew
              publishedDate={publishedDate}
              lastChangedDate={lastChangedDate}
              hasUnsaved={hasUnsavedNoumLayout || hasUnsaved}
              hasUnsavedPermissions={hasUnsavedPermissions}
              hasDraft={hasDraftNoumLayout || hasDraft}
              hasPublished={hasPublishedNoumLayout || hasPublished}
              spaceId={id}
              space={space}
              emptyElementErrorMessage={emptyElementErrorMessage}
              onChamberRestore={handleChamberRestore}
              isRestored={isRestored}
              isNavigateToCustomPreview={isNavigateToCustomPreview}
              cancelNavigateToCustomPreview={() =>
                setNavigateToCustomPreview(false)
              }
              onNavigate={handleNavigate}
              onSelectEditOption={sideBarOptionSelected}
              isOpenRestoreModal={openRestoreModal}
              setOpenRestoreModal={setOpenRestoreModal}
            />
          </Header>

          <ChamberViewLayout
            isEditing
            header={<EditChamberHeader />}
            onSelectEditOption={sideBarOptionSelected}
            hasThemePanel={openThemePanel || !!noumSidePanelId}
          >
            {(loading && !space) || isOnLoad ? (
              <Stack>
                <Spinner />
              </Stack>
            ) : (
              <EditNoumBody
                setNoumSidePanelId={setNoumSidePanelId}
                noumSidePanelId={noumSidePanelId}
                setNoumSidePanelType={setNoumSectionToolType}
              />
            )}
            <ThemePanel
              noumId={id}
              open={openThemePanel}
              onClose={() => setOpenThemePanel(false)}
            />
            <NoumSidePanel
              noumSidePanelType={noumSectionToolType!}
              open={!!noumSidePanelId}
              onClose={() => setNoumSidePanelId(undefined)}
              noumSidePanelId={noumSidePanelId!}
            />
          </ChamberViewLayout>

          {space?.type === SpaceTypeEnum.Project && (
            <ChamberEditMode
              isOpen={isOpen && !isVisited}
              handleClose={handleClose}
              handleMarkAsVisited={handleMarkAsVisited}
              markSpaceAsEditedLoading={markSpaceAsEditedLoading}
            />
          )}
          {space.link ? (
            <UnlinkOnArchiveNoumModal
              isOpen={openArchive}
              handleClose={() => setOpenArchive(false)}
              handleUnlinking={unlinkOnArchive}
              loading={unlinkArchivingLoader}
            />
          ) : (
            space?.name && (
              <ChamberArchiveModal
                noumName={space?.name}
                isOpen={openArchive}
                handleClose={() => setOpenArchive(false)}
                onArchive={onArchive}
              />
            )
          )}
          {space?.projectType && (
            <ChamberVisibilityInviteModal
              spaceId={id}
              connections={connections}
              linkedNoums={linkedNoums}
              visibility={space?.projectType}
              isOpen={openInvites}
              isSEOEnabled={!!space?.enableAds}
              handleClose={() => setOpenInvites(false)}
            />
          )}
          <ChamberPermissionModal
            spaceId={id}
            connections={approvedConnections!}
            loading={approvedConnectionsLoading}
            isOpen={openPermission}
            onClose={() => setOpenPermission(false)}
            onInvite={() => sideBarOptionSelected('invites')}
            linkedCount={linkedNoumsCount}
            infiniteState={approvedInfiniteState}
            fetchMoreConnections={fetchMoreApprovedConnections}
          />
          {openNoumAds && (
            <NoumAdsModal
              isOpen={openNoumAds}
              onClose={() => setOpenNoumAds(false)}
              refetchSpaceById={refetchSpaceById}
            />
          )}
          <ViewBroadcastModal
            infiniteState={infiniteState}
            campaigns={campaigns}
            campaignsLoading={campaignsLoading}
            fetchMoreCampaigns={fetchMoreCampaigns}
            spaceId={id}
            isOpen={openBroadcast}
            onRefetchCampaigns={onRefetchCampaigns}
            onClose={() => setOpenBroadcast(false)}
            onCampaign={() => setShowNewBroadcast(true)}
          />
          {showNewBroadcast && (
            <CreateBroadcastModal
              noumType={space.projectType}
              noumId={id}
              isOpen={showNewBroadcast}
              onClose={closeNewBroadcast}
              onSuccessfulCampaignCreation={onRefetchCampaigns}
            />
          )}
          <DeleteModal
            isOpen={isDeleteModal}
            onClose={() => setisDeleteModal(false)}
            type={noumSectionToolType!}
            onConfirm={deleteHandler}
            loading={removeToolLoading || removeSectionLoading}
          />
          {openNoumenaCopilot && (
            <NoumenaCopilot
              noumId={id}
              open={openNoumenaCopilot}
              onClose={() => setOpenNoumenaCopilot(false)}
            />
          )}
          {openManageMembers && (
            <ManageMembersModal
              isOpen={openManageMembers}
              handleClose={() => setOpenManageMembers(false)}
            />
          )}
        </AppStyled>
      )}
      {isSettingTheme && <Spinner />}
    </EditChamberProvider>
  );
};

export default EditNoum;
