import { forwardRef, type Ref, useCallback, useContext, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { type Transition } from 'history';
import { useParams } from 'react-router-dom';
import { Icon } from '@/components/Icon';
import ChamberPublish from '@/screens/Chamber/components/modals/ChamberPublish';
import ChamberSaveAsDraft from '@/screens/Chamber/components/modals/ChamberSaveAsDraft';
import { ChamberDiscardChange } from '@/screens/Chamber/components/modals/ChamberDiscardChange';
import { useWindowDimensions } from '@/hooks/dimensions';
import useBlocker from '@/hooks/useBlocker';
import { ChamberRestoreModal } from '@/screens/Chamber/components/modals/ChamberActionModal/Restore/ChamberRestoreModal';
import { useAuth } from '@/features/auth/contexts';
import {
  // useCheckChamberCanBePublished,
  useLaunchDarkly,
} from '@/hooks';
import { DisablePublishOrDraftContext } from '@/providers';
import { ChamberPublishVisibilityInfo } from '@/screens/Chamber/components/modals/ChamberPublishVisibilityInfo';
import SetupNoumModal from '@/screens/Plan/SetupNoumModal';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { IconWrapper } from '@/components/Header/styles';
import {
  EditHeaderWrapper,
  StatusLabel,
  StyledButton,
  StatusWrapper,
  MobileStatusWrapper,
  EditChangeStatusText,
  ButtonsWrapper,
  LastUpdateLabel,
} from './styles';
import { type EditHeaderProps } from './types';

export const EditHeader = forwardRef(
  (
    {
      publishedDate = '',
      lastChangedDate = '',
      hasUnsaved = false,
      hasUnsavedPermissions = false,
      hasDraft = false,
      hasPublished = false,
      spaceId,
      space,
      emptyElementErrorMessage,
      onChamberRestore,
      isRestored = false,
      isNavigateToCustomPreview,
      cancelNavigateToCustomPreview,
      onNavigate,
    }: EditHeaderProps,
    ref: Ref<HTMLDivElement>,
  ) => {
    const { t } = useTranslation();
    const { id = '' } = useParams();
    const { disableUpdate } = useContext(DisablePublishOrDraftContext);
    const [isOpenPublishModal, togglePublishModal] = useState<boolean>(false);
    const [isOpenVisibilityInfoModal, toggleVisibilityInfoModal] =
      useState<boolean>(false);
    const [isOpenSaDModal, toggleSaDModal] = useState<boolean>(false);
    const [isOpenExitModal, toggleExitModal] = useState<boolean>(false);
    const [isOpenRestoreModal, toggleRestoreModal] = useState<boolean>(false);
    const { masterId: mainSpaceId, isPending: isUserPending } = useAuth();
    const [setUpSlotForNoum, setSetUpSlotForNoum] = useState<boolean>(false);
    const { onRefetchSpaceByConfig, onRefetchSpaceById } = useNoumContext();

    // const { checkChamberCanBePublished } = useCheckChamberCanBePublished();

    const { flags } = useLaunchDarkly();

    const handleBlockNavigation = ({ retry }: Transition) => {
      if (hasUnsaved) {
        toggleExitModal(true);
      } else {
        retry();
      }
    };

    useBlocker(handleBlockNavigation, hasUnsaved && !isNavigateToCustomPreview);

    const handleClickPublish = async () => {
      togglePublishModal(true);
      // await checkChamberCanBePublished(
      //   id,
      //   togglePublishModal,
      //   setSetUpSlotForNoum,
      //   space?.type,
      // );
    };

    const handleClickDraft = () => {
      toggleSaDModal(true);
    };

    const handleClickExit = useCallback(() => {
      if (hasUnsaved) {
        toggleExitModal(true);
      } else onNavigate(id);
    }, [hasUnsaved, id, onNavigate]);

    const handleClosePublishModal = useCallback(
      (isSuccess?: boolean) => {
        togglePublishModal(false);
        if (isSuccess) {
          onRefetchSpaceByConfig();
          onRefetchSpaceById();
          onNavigate(id, `/noum/${id}`);
        }
      },
      [onRefetchSpaceByConfig, onRefetchSpaceById, onNavigate, id],
    );

    const handleCloseVisibilityInfoModal = () => {
      toggleVisibilityInfoModal(false);
      togglePublishModal(true);
    };

    const { width } = useWindowDimensions();

    const isTablet = width > 768;

    const handleCloseSaDModal = () => {
      toggleSaDModal(false);
    };

    const handleCloseExitModal = useCallback(
      (isSuccess?: boolean) => {
        toggleExitModal(false);
        if (isSuccess) {
          onNavigate(id);
        }
        cancelNavigateToCustomPreview?.();
      },
      [cancelNavigateToCustomPreview, id, onNavigate],
    );

    const handleClickRestore = () => {
      toggleRestoreModal(true);
    };

    const RenderMobileStatus = () => (
      <StatusWrapper className="xs-block">
        <MobileStatusWrapper>
          {hasUnsaved || hasDraft ? (
            <>
              {hasUnsaved || (hasUnsaved && hasDraft) ? (
                <EditChangeStatusText colorToken="--text-top-nav-danger-primary-default">
                  {t('noumena.header.unsaved_changes.text')}
                </EditChangeStatusText>
              ) : hasDraft && !hasUnsaved ? (
                <EditChangeStatusText colorToken="--text-top-nav-neutral-default">
                  {t('noumena.header.drafted_changes.text')}
                </EditChangeStatusText>
              ) : (
                <></>
              )}
              {hasPublished && !isRestored && (
                <LastUpdateLabel onClick={handleClickRestore}>
                  {t('noumena.header.restore_last_saved.text')}
                  <br />
                </LastUpdateLabel>
              )}
            </>
          ) : (
            <></>
          )}
        </MobileStatusWrapper>
      </StatusWrapper>
    );

    const RenderStatus = ({ className: name = 'xs-hidden' }) => (
      <StatusWrapper className={`xs-${name}`}>
        {hasUnsaved || hasDraft || isRestored ? (
          <>
            <StatusLabel
              data-testid={`lastChangedLabel_xs_${name}`}
              labelSize="small"
            >
              {t('noumena.header.last_changed.text')}: {lastChangedDate}
              {hasUnsaved || (hasUnsaved && hasDraft) ? (
                <EditChangeStatusText colorToken="--text-top-nav-danger-primary-default">
                  {t('noumena.header.unsaved_changes.text')}
                </EditChangeStatusText>
              ) : hasDraft && !hasUnsaved ? (
                <EditChangeStatusText colorToken="--text-top-nav-neutral-default">
                  {t('noumena.header.drafted_changes.text')}
                </EditChangeStatusText>
              ) : (
                <></>
              )}
            </StatusLabel>
            {hasPublished && (hasUnsaved || hasDraft) && (
              <LastUpdateLabel
                data-testid={`publishedDateLabel_xs_${name}`}
                onClick={handleClickRestore}
              >
                {t('noumena.header.restore_last_saved.text')} ({publishedDate}
                )
                <br />
              </LastUpdateLabel>
            )}
          </>
        ) : (
          (hasPublished || isRestored) && (
            <StatusLabel
              labelSize="small"
              data-testid={`lastPublishedLabel_xs_${name}`}
              className={`xs-${name}`}
            >
              {t('noumena.header.last_published.text')}: {publishedDate}
              <EditChangeStatusText colorToken="--text-top-nav-neutral-default">
                {t('noumena.header.all_changes_saved.text')}
              </EditChangeStatusText>
            </StatusLabel>
          )
        )}
      </StatusWrapper>
    );

    return (
      <EditHeaderWrapper ref={ref} data-testid="Edit-Header">
        <IconWrapper>
          <Icon
            data-testid="Header-Back-Button"
            name="arrow_left_m"
            color="--icon-button-neutral-default"
            size={24}
            onClick={handleClickExit}
          />
        </IconWrapper>
        <RenderStatus className="hidden" />
        <ButtonsWrapper>
          <StyledButton
            data-testid="Header-SaveAsADraft"
            secondary
            size="small"
            onClick={handleClickDraft}
            disabled={!hasUnsaved || disableUpdate}
          >
            {t('noumena.header.draft_button.text')}
          </StyledButton>
          <StyledButton
            data-testid="Header-Publish-Button"
            primary
            intent="positive"
            size="small"
            onClick={handleClickPublish}
            disabled={
              (!hasUnsaved &&
                !hasDraft &&
                !hasUnsavedPermissions &&
                !isRestored) ||
              disableUpdate ||
              isUserPending
            }
          >
            {t('noumena.header.publish_button.text')}
          </StyledButton>
        </ButtonsWrapper>

        {isTablet ? <RenderStatus className="block" /> : <RenderMobileStatus />}
        <ChamberPublish
          spaceId={spaceId}
          space={space}
          isOpen={isOpenPublishModal}
          handleClose={handleClosePublishModal}
          setSetUpSlotForNoum={setSetUpSlotForNoum}
          handleClickInfo={() => {
            toggleVisibilityInfoModal(true);
          }}
          emptyElementErrorMessage={emptyElementErrorMessage}
          isHomeNoum={space?._id === mainSpaceId}
        />
        <ChamberPublishVisibilityInfo
          isOpen={isOpenVisibilityInfoModal}
          handleClose={handleCloseVisibilityInfoModal}
        />
        <ChamberSaveAsDraft
          spaceId={spaceId}
          space={space}
          isOpen={isOpenSaDModal}
          handleClose={handleCloseSaDModal}
          emptyElementErrorMessage={emptyElementErrorMessage}
        />
        <ChamberDiscardChange
          spaceId={spaceId}
          space={space}
          isOpen={
            isOpenExitModal || (hasUnsaved && !!isNavigateToCustomPreview)
          }
          handleClose={handleCloseExitModal}
        />
        <ChamberRestoreModal
          spaceId={spaceId}
          isOpen={isOpenRestoreModal}
          version={publishedDate}
          cancelCallback={() => toggleRestoreModal(false)}
          sucessCallback={() => {
            toggleRestoreModal(false);
            onChamberRestore?.();
          }}
          emptyElementErrorMessage={emptyElementErrorMessage}
        />
        {flags.paymentSubscriptions && setUpSlotForNoum && (
          <SetupNoumModal
            open={setUpSlotForNoum}
            onClose={() => setSetUpSlotForNoum(false)}
            chamberIdAfterCreatingNoum={id}
          />
        )}
      </EditHeaderWrapper>
    );
  },
);
