import { useCallback, useEffect, useMemo, useState } from 'react';
import BasicChipsTabsForm from '@/components/Tabs/TabsForm';
import { type TabsHeaderTypes } from '@/features/noums/components/Toolbox';
import {
  type AddNoumLayoutToolInput,
  ElementTypeEnum,
  SpaceTypeEnum,
} from '@/apollo/generated/types';
import { ElementUtils } from '@/utils/element';
import { Spinner } from '@/components/Spinner';
import { useEditChamberState } from '@/screens/Chamber/EditChamber/provider';
import {
  SectionToolType,
  TextAlignType,
} from '@/screens/Chamber/components/NoumSections/constants';
import { Tabs } from '@/features/noums/hooks/Toolbox/constants';
import {
  useAddNoumLayoutToolHelper,
  useUpdateNoumSectionHelper,
} from '@/features/noums/hooks/spaceQuery';
import { type ToolboxProps } from './types';
import { ToolboxTabContent } from './ToolboxTabContent';
import { ToolboxLoader, ToolboxTabs, ToolboxWrapper } from './styles';

export const Toolbox = ({
  spaceId,
  columnId,
  handleSelectElementType,
  position = 1,
  baseElementId,
  sectionId,
}: ToolboxProps) => {
  const { handleEditModal, setsectionSideBarOptions, sections, space } =
    useEditChamberState();
  const [selectedId, setSelectedId] = useState(Tabs[0].id);
  const [activeTab, setActiveTab] = useState(Tabs[0]);
  const handleChange = useCallback(
    (id: string) => {
      if (id && id !== selectedId) {
        setSelectedId(id);
      }
    },
    [selectedId],
  );
  const listOfTabs: TabsHeaderTypes[] = useMemo(
    () =>
      Tabs.filter(
        (tab) =>
          !(space?.type === SpaceTypeEnum.Home && tab.group === 'finance'),
      ).map((tab) => ({ ...tab, labelSize: 'auto' })),
    [space?.type],
  );

  const isSectionBackground = useMemo(
    () =>
      sections.find((section) => section._id === sectionId)?.background ===
      false,
    [sectionId, sections],
  );

  useEffect(() => {
    const selectedTab = Tabs.find((tab) => tab.id === selectedId);
    setActiveTab(selectedTab || Tabs[0]);
  }, [selectedId]);

  const { addNoumLayoutToolHelper, loading: addNoumLayoutToolLoading } =
    useAddNoumLayoutToolHelper();
  const { updateNoumSectionHelper } = useUpdateNoumSectionHelper();
  const onSelectElementType = useCallback(
    async (elementType: ElementTypeEnum) => {
      if (spaceId) {
        const newElementInput: AddNoumLayoutToolInput = {
          elementType,
          bodyContentType:
            ElementUtils.getBodyContentTypeFromElementType(elementType),
          columnId,
          position: Math.round(position),
        };
        if (
          elementType === ElementTypeEnum.Image ||
          elementType === ElementTypeEnum.Video
        ) {
          newElementInput.meta = {
            percentageSize: 30,
            align: TextAlignType.CENTER,
          };
        }
        const newElement = await addNoumLayoutToolHelper(
          spaceId,
          newElementInput,
          baseElementId,
          position,
        );
        if (newElement) {
          if (isSectionBackground) {
            setsectionSideBarOptions?.({
              sectionBackgroud: { id: sectionId!, background: true },
            });
            await updateNoumSectionHelper(spaceId, {
              sectionId,
              background: true,
            });
          }
          if (!addNoumLayoutToolLoading) {
            handleSelectElementType();
          }
          if (
            elementType === ElementTypeEnum.Image ||
            elementType === ElementTypeEnum.Video
          )
            return;
          handleEditModal?.(
            SectionToolType.TOOL_TYPE,
            newElement?._id || '',
            newElement,
          );
        }
      }
    },
    [
      spaceId,
      columnId,
      position,
      addNoumLayoutToolHelper,
      baseElementId,
      addNoumLayoutToolLoading,
      handleEditModal,
      isSectionBackground,
      handleSelectElementType,
      setsectionSideBarOptions,
      sectionId,
      updateNoumSectionHelper,
    ],
  );

  return (
    <ToolboxWrapper>
      {addNoumLayoutToolLoading && (
        <ToolboxLoader>
          <Spinner />
        </ToolboxLoader>
      )}
      <ToolboxTabs>
        <BasicChipsTabsForm
          onChange={handleChange}
          inputList={listOfTabs}
          selectedId={selectedId}
          mode="isBackground"
          isWithoutImage
          fontSize="--font-input-small-size"
        />
      </ToolboxTabs>
      <ToolboxTabContent
        handleSelectElementType={onSelectElementType}
        activeTab={activeTab}
      />
    </ToolboxWrapper>
  );
};

export default Toolbox;
